// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import VueRouter from "vue-router";

Vue.use(VueRouter);
import Home from "./components/Home.vue";
import Product from "./components/Product.vue";
import HowTo from "./components/HowTo.vue";
import Faq from "./components/Faq.vue";
import FourOFour from "./components/404.vue";

const routes = [
  { path: "/", component: Home },
  { path: "/product/:id", component: Product },
  { path: "/howto", component: HowTo },
  { path: "/faq", component: Faq },
  { path: "*", component: FourOFour }
];

const router = new VueRouter({
  mode: "history",
  routes
});

Vue.config.productionTip = false;

// some some
const wheel = document.createElement("script");
wheel.setAttribute(
  "src",
  "https://use.fontawesome.com/releases/v5.7.2/js/all.js"
);
document.head.appendChild(wheel);

Vue.filter("capitalize", function(string) {
  if (string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
});

import Cloudinary from "cloudinary-vue";
Vue.use(Cloudinary, {
  configuration: {
    cloudName: "obavigo"
  }
});

/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: { App },
  template: "<App/>",
  mounted() {
    const navItem = document.querySelector("a.toggle-nav");
    const navList = document.querySelector(".nav-list");
    var demi = document.getElementsByClassName("demilade");
    var demimi = document.getElementById("demimi");

    demimi.addEventListener("click", function() {
      navList.classList.remove("active");
    });

    function toggleNavigation() {
      navList.classList.toggle("active");
    }
    // function removeHeaderclass() {
    //   navList.classList.remove("active");
    // }

    Array.from(demi).forEach(v =>
      v.addEventListener("click", function() {
        navList.classList.remove("active");
      })
    );

    navItem.addEventListener("click", toggleNavigation);
    // demi.addEventListener("click", removeHeaderclass);
  }
});



